.burger {
	cursor: pointer;
	@include size(40px);
	display: flex;
	align-items: center;

	span {
		position: relative;
		display: inline-block;
		width: 100%;
		height: 2px;
		vertical-align: bottom;
		background: #fff;
		transition: background .2s .4s;

		&:before, &:after {
			content: "";
			position: absolute;
			background: #fff;
			transform-origin: 50% 50%;
			transition: top .3s .3s,transform .3s;
			// border-radius: 2px
			width: 100%;
			height: 2px;
			left: 0; }

		&:before {
			// transform: none
			top: 10px; }

		&:after {
			top: -10px; } } }

.mobile-menu--open {
	.burger > span {
		background-color: transparent !important;
		&:before {
			transform: rotate3d(0, 0, 1, 45deg) translate3d(0, 0, 0) !important;
			top: 0;
			transition: top .3s,transform .3s .5s; }
		&:after {
			transform: rotate3d(0, 0, 1, -45deg) translate3d(0, 0, 0) !important;
			top: 0;
			transition: top .3s,transform .3s .5s; } } }
