		background-size: contain !important;
		position: relative;
		width: auto !important; }
	&__roll_block_top {
		height: calc(100% - 10px);
		// width: 100%
		width: auto !important;
		background: url("../img/photos/product-scroll-top.png") 8px 0 no-repeat;
		background-size: auto 100% !important;
		position: absolute;
		right: 4px;
		left: 50%;
		// bottom: -3px
		top: 7px;
		border-radius: 18px 18px; }
	&__roll_block_slider {
		position: relative;
		height: 25px;
		margin-top: 25px;
		width: auto !important; }
	&__roll_block_slider_left {
		background-color: #3cb3d4;
