		z-index: 1;
		width: auto !important; }
	&__roll_block_slider_right {
		background-color: #cccccc;
		right: 0;
		left: 50%;
		position: absolute;
		height: 1px;
		z-index: 2;
		width: auto !important; }
	&__roll_block_slider_btn {
		position: absolute;
		left: 50%;
		top: 0;
		width: 36px !important;
		height: 24px;
		margin-top: -12px;
		cursor: pointer;
		background-color: #fff;
		z-index: 3;
		border-radius: 12px;
		&:hover {
			&:before {
				transform: translateX(-3px); }
			&:after {
				transform: translateX(3px); } }
		&:before, &:after {
			content: "";
			top: 0;
			width: 18px;
			height: 100%;
			position: absolute;
			background-image: url("../img/scroll-btn.png");
			transition: 0.5s; }
		&:before {
			left: 0; }
		&:after {
			right: 0;
			background-position: top right; } } }
